#homePage {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--light-gray);
}
#homePage > p {
  color: var(--dark-gray);
  background-color: var(--light-gray);
  font-size: 1.5em;
  z-index: 10;
  width: 60%;
}
#homePage > u {
  border-bottom: 10px solid var(--dark-gray);
  width: 80%;
  z-index: 10;
  border-right: 50px solid transparent;
  border-left: 50px solid transparent;
  filter: drop-shadow(5px 5px 0px black);
}
#homePage > video {
  width: 100%;
  margin-bottom: -400px;
  opacity: 100;
}
.homeVideo {
  display: flex;
  align-items: flex-start;
  width: 100%;
}
.homeVideo > video {
  height: 400px;
}
.homeVideo > div {
  width: 40vw;
  border-top: 400px solid var(--dark-gray);
  border-right: 400px solid transparent;
}
#homePage > .right {
  flex-direction: row-reverse;
} 
#homePage > .right > div {
  border-right: none;
  border-top: 400px solid transparent;
  border-left: 400px solid var(--dark-gray);
} 
#threeBackground {
  position: fixed;
  top: 0;
  bottom: 0;
  margin: 0;
  z-index: -10;
}
#threeBackground > .particle {
  position: fixed;
  height: 100%;
  width: 100%;
  z-index: -1000;
  background-color: black;
}
@media only screen and (max-device-width: 1362px) {
  #homePage {
    font-size: 20px;
  }
  #homePage > video {
    width: 100%;
    margin-bottom: -200px;
    margin-top: 40px;
  }
  .homeVideo > video {
    height: 300px;
  }
  .homeVideo > div {
    height: 300px;
    border-top: 300px solid var(--dark-gray);
    border-right: 300px solid transparent;
  }
  #homePage > .right > div {
    border-right: none;
    border-top: 300px solid transparent;
    border-left: 300px solid var(--dark-gray);
  } 
}

@media only screen and (max-device-width: 853px) {
  #homePage {
    font-size: 15px;
    margin-left: 3%;
  }
  #homePage > p {
    width: 90%;
  }
  #homePage > video {
    width: 100%;
    margin-top: 40px;
    margin-bottom: -100px;
  }
  .homeVideo > video {
    height: 300px;
  }
  .homeVideo > div {
    height: 300px;
    border-top: 300px solid var(--dark-gray);
    border-right: 300px solid transparent;
  }
  #homePage > .right > div {
    border-right: none;
    border-top: 300px solid transparent;
    border-left: 300px solid var(--dark-gray);
  } 
  #homePage > u {
    border-width: 5px;
    border-left-width: 20px;
    border-right-width: 20px;
  }
}

@media only screen and (max-device-width: 480px) {
  #homePage {
    font-size: 8px;
    margin-left: 3%;
  }
  #homePage > p {
    width: 90%;
    height: 40vh
  }
  #homePage > video {
    width: 100%;
    margin-top: 40px;
    margin-bottom: -60px;
  }
  .homeVideo > video {
    height: 300px;
  }
  .homeVideo > div {
    height: 300px;
    border-top: 300px solid var(--dark-gray);
    border-right: 300px solid transparent;
  }
  #homePage > .right > div {
    border-right: none;
    border-top: 300px solid transparent;
    border-left: 300px solid var(--dark-gray);
  } 
  #homePage > u {
    border-width: 5px;
    border-left-width: 20px;
    border-right-width: 20px;
  }
}